rompt:empty {\n    padding-top: 0;\n    padding-bottom: 0;\n}\n\ndiv.unrecognized_cell {\n    // from text_cell\n    padding: 5px @_cell_padding_minus_border 5px 0px;\n    .hbox();\n\n    .inner_cell {\n        .border-radius(@border-radius-base);\n        padding: @_cell_padding_minus_border;\n        font-weight: bold;\n        color: red;\n        border: @cell_border_width solid @light_border_color;\n        background: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 8.2%));\n        // remove decoration from link\n        a {\n            color: inherit;\n            text-decoration: none;\n\n            &:hover {\n                color: inherit;\n                text-decoration: none;\n            }\n        }\n    }\n}\n@media (max-width: @screen-xs-min) {\n    // remove prompt indentation on small screens\n    div.unrecognized_cell > div.prompt {\n        display: none;\n    }\n}\n","div.code_cell {\n    /* avoid page breaking on code cells when printing */\n    @media print {\n        page-break-inside: avoid;\n    }\n}\n\n/* any special styling for code cells that are currently running goes here */\ndiv.code_cell.running {\n}\n\ndiv.input {\n    page-break-inside: avoid;\n    .hbox();\n}\n\n@media (max-width: @screen-xs-min) {\n    // move prompts above code on small screens\n    div.input {\n        .vbox();\n    }\n}\n\ndiv.prompt_container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: flex-start;\n    text-align: right;\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_prompt {\n    color: @input_prompt_color;\n    border-top: 1px solid transparent;\n}\n\ndiv.run_this_cell {\n    display: none;\n    cursor: pointer;\n    color: #333;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    padding-left: 2ex;\n    padding-right: 2ex;\n    width: 1ex;\n}\n\n// set the prompt width\ndiv.prompt {\n    min-width: 15ex;\n}\n\n@media (-moz-touch-enabled: 1), (any-pointer: coarse) {\n    div.run_this_cell {\n        display: block;\n    }\n}\n\n// The styles related to div.highlight are for nbconvert HTML output only. This works\n// because the .highlight div isn't present in the live notebook. We could put this into\n// nbconvert, but it easily falls out of sync, can't use our less variables and doesn't\n// help the basic template when paired with our CSS.\n\ndiv.input_area > div.highlight {\n    margin: @code_padding;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n\ndiv.input_area > div.highlight > pre {\n    margin: 0px;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n","/* The following gets added to the <head> if it is detected that the user has a\n * monospace font with inconsistent normal/bold/italic height.  See\n * notebookmain.js.  Such fonts will have keywords vertically offset with\n * respect to the rest of the text.  The user should select a better font.\n * See: https://github.com/ipython/ipython/issues/1503\n *\n * .CodeMirror span {\n *      vertical-align: bottom;\n * }\n */\n\n.CodeMirror {\n    line-height: @code_line_height;  /* Changed from 1em to our global default */\n    font-size: @notebook_font_size;\n    height: auto;     /* Changed to auto to autogrow */\n    background: none;  /* Changed from white to allow our bg to show through */\n}\n\n.CodeMirror-scroll {\n    /*  The CodeMirror docs are a bit fuzzy on if overflow-y should be hidden or visible.*/\n    /*  We have found that if it is visible, vertical scrollbars appear with font size changes.*/\n    overflow-y: hidden;\n    overflow-x: auto;\n}\n\n.CodeMirror-lines {\n    /* In CM2, this used to be 0.4em, but in CM3 it went to 4px. We need the em value because */\n    /* we have set a different line-height and want this to scale with that. */\n    /* Note that this should set vertical padding only, since CodeMirror assumes\n       that horizontal