) - 63%));\n@blockquote-font-size: inherit;\n@modal-inner-padding: 15px;\n@grid-float-breakpoint: 541px;\n@screen-xs: 540px;\n@logo_height: 28px;\n@border-radius-small: 1px;\n@border-radius-base: 2px;\n@border-radius-large: 3px;\n@grid-gutter-width: 0px;\n@kbd-color: #888;\n@kbd-bg: transparent;\n\n@icon-font-path: \"~bootstrap/fonts/\";\n\n// Disable modal slide-in from top animation.\n.modal {\n  &.fade .modal-dialog {\n    .translate(0, 0);\n  }\n}\n\n// Set the default code color.\ncode {\n  color: @black; // default code color in bootstrap is #d14 (crimson / amaranth)\n}\n\n// Override bootstrap pre element styling.\npre {\n  // bootstrap has pre defaults that we don't want to inherit.\n  // start pre tag defaults based on the surrounding context instead.\n  font-size: inherit;\n  line-height: inherit;\n}\n\n// Disable bold labels in BS3\nlabel {\n    font-weight: normal;\n}\n\n// Our own global variables for all pages go here\n@global-shadow: 0px 0px 12px 1px rgba(87, 87, 87, 0.2);\n@global-shadow-dark: 0px 0px 12px 1px rgba(87, 87, 87, 0.4);\n@page-header-padding: 20px;\n/* Make the page background atleast 100% the height of the view port */\n@page-backdrop-height: 100vh;\n/* Make the page itself atleast 70% the height of the view port */\n@page-min-height: 0;\n@page-backdrop-color: var(--page-backdrop-color);\n@body-bg: var(--body-bg);\n@page-color: @body-bg;\n@page-padding: 15px;\n\n// preven container size to jump from 768px to 720px\n// when window width go from 768 to 769+\n@container-sm : @screen-sm-min;\n","// Mixin CSS classes\n\n.border-box-sizing {\n    box-sizing: border-box;\n    -moz-box-sizing: border-box;\n    -webkit-box-sizing: border-box;\n}\n\n.corner-all {\n    border-radius: @border-radius-base;\n}\n\n.border-radius(@radius) {\n  border-radius: @radius;\n}\n\n.no-padding {\n    padding: 0px;\n}\n","\n/* Flexible box model classes */\n/* Taken from Alex Russell http://infrequently.org/2009/08/css-3-progress/ */\n\n/* This file is a compatibility layer.  It allows the usage of flexible box \nmodel layouts accross multiple browsers, including older browsers.  The newest,\nuniversal implementation of the flexible box model is used when available (see\n`Modern browsers` comments below).  Browsers that are known to implement this \nnew spec completely include:\n\n    Firefox 28.0+\n    Chrome 29.0+\n    Internet Explorer 11+ \n    Opera 17.0+\n\nBrowsers not listed, including Safari, are supported via the styling under the\n`Old browsers` comments below.\n*/\n\n\n.hbox {\n    /* Old browsers */\n    display: -webkit-box;\n    -webkit-box-orient: horizontal;\n    -webkit-box-align: stretch;\n\n    display: -moz-box;\n    -moz-box-orient: horizontal;\n    -moz-box-align: stretch;\n\n    display: box;\n    box-orient: horizontal;\n    box-align: stretch;\n\n    /* Modern browsers */\n    display: flex;\n    flex-direction: row;\n    align-items: stretch;\n}\n\n.hbox > * {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n\n    /* Modern browsers */\n    flex: none;\n}\n\n.vbox {\n    /* Old browsers */\n    display: -webkit-box;\n    -webkit-box-orient: vertical;\n    -webkit-box-align: stretch;\n\n    display: -moz-box;\n    -moz-box-orient: vertical;\n    -moz-box-align: stretch;\n\n    display: box;\n    box-orient: vertical;\n    box-align: stretch;\n\n    /* Modern browsers */\n    display: flex;\n    flex-direction: column;\n    align-items: stretch;\n}\n\n.vbox > * {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n    \n    /* Modern browsers */\n    flex: none;\n}\n\n.hbox.reverse,\n.vbox.reverse,\n.reverse {\n    /* Old browsers */\n    -webkit-box-direction: reverse;\n    -moz-box-direction: reverse;\n    box-direction: reverse;\n    \n    /* Modern browsers */\n    flex-direction: row-reverse;\n}\n\n.hbox.box-flex0,\n.vbox.box-flex0,\n.box-flex0 {\n    /* Old browsers */\n    -webkit-box-flex: 0;\n    -moz-box-flex: 0;\n    box-flex: 0;\n    \n    /* Modern browsers */\n    flex: none;\n    width: auto;\n}\n\n.hbox.box-flex1,\n.vbox.box-flex1,\n.box-flex1 {\n    /* Old browsers */\n    -webkit-box-flex: 1;\n    -moz-box-flex: 1;\n    box-flex: 1;\n\n    /* Modern browsers */\n    flex: 1;\n}\n\n.hbox.box-flex,\n.vbox.box-flex,\n.box-flex {\n    /* Old browsers */\n    .box-flex1();\n}\n\n.hbox.box-flex2,\n.vbox.box-flex2,\n.box-flex2 {\n    /* Old browsers */\n    -webkit-box-flex: 2;\n    -moz-box-flex: 2;\n    box-flex: 2;\n\n    /* Modern browsers */\n    flex: 2;\n}\n\n.box-group1 {\n    /*  Deprecated */\n    -webkit-box-flex-group: 1;\n    -moz-box-flex-group: 1;\n    box-flex-group: 1;\n}\n\n.box-group2 {\n    /* Deprecated */\n    -webkit-box-flex-group: 2;\n    -moz-box-flex-group: 2;\n    box-flex-group: 2;\n}\n\n.hbox.start, \n.vbox.start,\n.start {\n    /* Old browsers */\n    -webkit-box-pack: start;\n    -moz-box-pack: start;\n    box-pack: start;\n    \n    /* Modern browsers */\n    justify-content: flex-start;\n}\n\n.hbox.end, \n.vbox.end,\n.end {\n    /* Old browsers */\n    -webkit-box-pack: end;\n    -moz-box-pack: end;\n    box-pack: end;\n    \n    /* Modern browsers */\n    justify-content: flex-end;\n}\n\n.hbox.center, \n.vbox.center,\n.center {\n    /* Old browsers */\n    -webkit-box-pack: center;\n    -moz-box-pack: center;\n    box-pack: center;\n    \n    /* Modern browsers */\n    justify-content: center;\n}\n\n.hbox.baseline, \n.vbox.baseline,\n.baseline {\n    /* Old browsers */\n    -webkit-box-pack: baseline;\n    -moz-box-pack: baseline;\n    box-pack: baseline;\n    \n    /* Modern browsers */\n    justify-content: baseline;\n}\n\n.hbox.stretch, \n.vbox.stretch,\n.stretch {\n    /* Old browsers */\n    -webkit-box-pack: stretch;\n    -moz-box-pack: stretch;\n    box-pack: stretch;\n    \n    /* Modern browsers */\n    justify-content: stretch;\n}\n\n.hbox.align-start, \n.vbox.align-start,\n.align-start {\n    /* Old browsers */\n    -webkit-box-align: start;\n    -moz-box-align: start;\n    box-align: start;\n    \n    /* Modern browsers */\n    align-items: flex-start;\n}\n\n.hbox.align-end, \n.vbox.align-end,\n.align-end {\n    /* Old browsers */\n    -webkit-box-align: end;\n    -moz-box-align: end;\n    box-align: end;\n    \n    /* Modern browsers */\n    align-items: flex-end;\n}\n\n.hbox.align-center, \n.vbox.align-center,\n.align-center {\n    /* Old browsers */\n    -webkit-box-align: center;\n    -moz-box-align: center;\n    box-align: center;\n    \n    /* Modern browsers */\n    align-items: center;\n}\n\n.hbox.align-baseline, \n.vbox.align-baseline,\n.align-baseline {\n    /* Old browsers */\n    -webkit-box-align: baseline;\n    -moz-box-align: baseline;\n    box-align: baseline;\n    \n    /* Modern browsers */\n    align-items: baseline;\n}\n\n.hbox.align-stretch, \n.vbox.align-stretch,\n.align-stretch {\n    /* Old browsers */\n    -webkit-box-align: stretch;\n    -moz-box-align: stretch;\n    box-align: stretch;\n    \n    /* Modern browsers */\n    align-items: stretch;\n}\n","div.error {\n  margin: 2em;\n  text-align: center;\n}\n\ndiv.error > h1 {\n    font-size: 500%;\n    line-height: normal;\n}\n\ndiv.error > p {\n    font-size: 200%;\n    line-height: normal;\n}\n\ndiv.traceback-wrapper {\n    text-align: left;\n    max-width: 800px;\n    margin: auto;\n    pre.traceback {\n      max-height: 600px;\n      overflow: auto;\n    }\n}\n","/**\n * Primary styles\n *\n * Author: Jupyter Development Team\n */\n\n\nbody {\n    background-color: @body-bg;\n    /* This makes sure that the body covers the entire window and needs to\n       be in a different element than the display: box in wrapper below */\n    position: absolute;\n    left: 0px;\n    right: 0px;\n    top: 0px;\n    bottom: 0px;\n    overflow: visible;\n    color: var(--text-color);\n}\n\nbody > #header {\n    /* Initially hidden to prevent FLOUC */\n    display: none;\n    background-color: @header-bg;\n\n    /* Display over codemirror */\n    position: relative;\n    z-index: 100;\n\n    #header-container {\n        display: flex;\n        flex-direction: row;\n        justify-content: space-between;\n        padding: 5px;\n        padding-bottom: 5px;\n        padding-top: 5px;\n        .border-box-sizing();\n    }\n\n    .header-bar {\n        width: 100%;\n        height: 1px;\n        background: @navbar-default-border;\n        margin-bottom: -1px;\n    }\n\n    @media print {\n        display: none !important;\n    }\n}\n\n#header-spacer {\n    width: 100%;\n    visibility: hidden;\n    \n    @media print {\n        display: none;\n    }\n}\n\n#ipython_notebook {\n    padding-left: 0px;\n    padding-top: (@navbar-height - @logo_height) / 2;\n    padding-bottom: (@navbar-height - @logo_height) / 2;\n    margin-right: auto;\n}\n\n[dir=\"rtl\"] #ipython_notebook {\n    margin-right: 10px;\n    margin-left: 0;\n}\n\n[dir=\"rtl\"] #ipython_notebook.pull-left {\n     .pull-right();\n}\n\n.flex-spacer {\n    flex: 1;\n}\n\n#noscript {\n     width: auto;\n     padding-top: 16px;\n     padding-bottom: 16px;\n     text-align: center;\n     font-size: 22px;\n     color: red;\n     font-weight: bold;\n}\n\n#ipython_notebook img {\n    height: @logo_height;\n}\n\n#site {\n    width: 100%;\n    display: none;\n    .border-box-sizing();\n    overflow: auto;\n    @media print {\n      // force auto-height on print (overrides manual resizing in live view)\n      height: auto !important;\n    }\n}\n\n/* Smaller buttons */\n.ui-button .ui-button-text {\n    padding: 0.2em 0.8em;\n    font-size: 77%;\n}\n\ninput.ui-button {\n    padding: 0.3em 0.9em;\n}\n\nspan#kernel_logo_widget {\n    margin: 0 10px;\n    margin-left: auto;\n}\n\nspan#login_widget {\n    float: right;\n}\n\n[dir=\"rtl\"] span#login_widget {\n    float: left;\n}\n\nspan#login_widget > .button,\n#logout, #shutdown\n{\n    .btn-default();\n    margin-left: 10px;\n}\n\n.nav-header {\n    text-transform: none;\n}\n\n#header > span {\n  margin-top: 10px;\n}\n\n// class for stretching dialogs to fill the screen\n.modal_stretch .modal-dialog {\n    .vbox();\n    min-height: 80vh;\n    .modal-body {\n        // ~\"foo\" is to avoid less turning this into a weird value\n        max-height: calc(~\"100vh - 200px\");\n        overflow: auto;\n        flex: 1;\n    }\n}\n\n.modal-header {\n  cursor: move;\n}\n\n@media (min-width: @screen-sm-min) {\n    .modal .modal-dialog {\n        width: 700px;\n    }\n}\n\n// less mixin to be sure to add the right class to get icons with font awesome.\n.icon(@ico){\n    .fa();\n    content: @ico;\n}\n\n@media (min-width: @screen-sm-min) {\n    select.form-control {\n        margin-left: @padding-base-horizontal;\n        margin-right: @padding-base-horizontal;\n    }\n}\n\n/* rtl fixes for the error, connecting, and renaming window */\n\n[dir=\"rtl\"] .modal-footer {\n    text-align : left !important;\n}\n\n[dir=\"rtl\"] .close {\n    float : left;\n}\n\n[dir=\"rtl\"] .fa-step-forward::before {\n    content: \"\\f048\";\n}\n","/* CSS font colors for translated ANSI escape sequences */\n\n.ansicolors(@name, @normal, @intense) {\n  .ansi-@{name}-fg { color: @normal; }\n  .ansi-@{name}-bg { background-color: @normal; }\n  .ansi-@{name}-intense-fg { color: @intense; }\n  .ansi-@{name}-intense-bg { background-color: @intense; }\n}\n\n/* The color values are a mix of\n   http://www.xcolors.net/dl/baskerville-ivorylight and\n   http://www.xcolors.net/dl/euphrasia */\n\n.ansicolors(black, #3E424D, #282C36);\n.ansicolors(red, #E75C58, #B22B31);\n.ansicolors(green, #00A250, #007427);\n.ansicolors(yellow, #DDB62B, #B27D12);\n.ansicolors(blue, #208FFB, #0065CA);\n.ansicolors(magenta, #D160C4, #A03196);\n.ansicolors(cyan, #60C6C8, #258F8F);\n.ansicolors(white, #C5C1B4, #A1A6B2);\n\n.ansi-default-inverse-fg { color: #FFFFFF; }\n.ansi-default-inverse-bg { background-color: #000000; }\n\n.ansi-bold { font-weight: bold; }\n.ansi-underline { text-decoration: underline; }\n","@_cell_padding_minus_border: @cell_padding - @cell_border_width;\n\n._selected_style(@border_color) {\n    position: absolute;\n    display: block;\n    top: -1 * @cell_border_width;\n    left: -1 * @cell_border_width;\n    width: @cell_left_border_width;\n    // height: 100% + (2 * @cell_border_width);\n    height: calc(~\"100% + \"(2 * @cell_border_width));\n    content: '';\n    background: @border_color;\n}\n\n\ndiv.cell {\n    .vbox();\n    .corner-all();\n    .border-box-sizing();\n    \n    border-width: @cell_border_width;\n    border-style: solid;\n    border-color: transparent;\n\n    width: 100%;\n    padding: @_cell_padding_minus_border;\n    /* This acts as a spacer between cells, that is outside the border */\n    margin: 0px;\n    outline: none;\n    \n    position: relative;\n    overflow: visible;\n\n    &:before {\n        ._selected_style(transparent);\n    }\n\n    &.jupyter-soft-selected {\n        border-left-color: @soft_select_color;\n        border-left-width: @cell_border_width;\n        padding-left: @cell_padding - @cell_border_width;\n        border-right-color: @soft_select_color;\n        border-right-width: @cell_border_width;\n        background: @soft_select_color;\n    \n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    &.selected, &.selected.jupyter-soft-selected {\n        border-color: @border_color;\n        \n        &:before {\n            ._selected_style(@selected_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n    .edit_mode &.selected {\n        border-color: @edit_mode_border_color;\n        \n        &:before {\n            ._selected_style(@edit_mode_border_color);\n        }\n\n        @media print {\n            border-color: transparent;\n        }\n    }\n\n}\n\n\n.prompt {\n    /* This needs to be wide enough for 3 digit prompt numbers: In[100]: */\n    min-width: 14ex;\n\n    /* This padding is tuned to match the padding on the CodeMirror editor. */\n    padding: @code_padding;\n    margin: 0px;\n    font-family: @font-family-monospace;\n    text-align: right;\n\n    /* This has to match that of the the CodeMirror class line-height below */\n    line-height: @code_line_height;\n\n    /* Don't highlight prompt number selection */\n    -webkit-touch-callout: none;\n    -webkit-user-select: none;\n    -khtml-user-select: none;\n    -moz-user-select: none;\n    -ms-user-select: none;\n    user-select: none;\n\n    /* Use default cursor */\n    cursor: default;\n}\n\n@media (max-width: @screen-xs-min) {\n    // prompts are in the main column on small screens,\n    // so text should be left-aligned\n    .prompt {\n        text-align: left;\n    }\n}\n\ndiv.inner_cell {\n    min-width: 0;\n    .vbox();\n    .box-flex1();\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_area {\n    border: 1px solid @light_border_color;\n    .corner-all;\n    background: @cell_background;\n    line-height: @code_line_height;\n}\n\n/* This is needed so that empty prompt areas can collapse to zero height when there\n   is no content in the output_subarea and the prompt. The main purpose of this is\n   to make sure that empty JavaScript output_subareas have no height. */\ndiv.prompt:empty {\n    padding-top: 0;\n    padding-bottom: 0;\n}\n\ndiv.unrecognized_cell {\n    // from text_cell\n    padding: 5px @_cell_padding_minus_border 5px 0px;\n    .hbox();\n\n    .inner_cell {\n        .border-radius(@border-radius-base);\n        padding: @_cell_padding_minus_border;\n        font-weight: bold;\n        color: red;\n        border: @cell_border_width solid @light_border_color;\n        background: hsl(var(--body-bg-hue), var(--body-bg-saturation), calc(var(--body-bg-light) - 8.2%));\n        // remove decoration from link\n        a {\n            color: inherit;\n            text-decoration: none;\n\n            &:hover {\n                color: inherit;\n                text-decoration: none;\n            }\n        }\n    }\n}\n@media (max-width: @screen-xs-min) {\n    // remove prompt indentation on small screens\n    div.unrecognized_cell > div.prompt {\n        display: none;\n    }\n}\n","div.code_cell {\n    /* avoid page breaking on code cells when printing */\n    @media print {\n        page-break-inside: avoid;\n    }\n}\n\n/* any special styling for code cells that are currently running goes here */\ndiv.code_cell.running {\n}\n\ndiv.input {\n    page-break-inside: avoid;\n    .hbox();\n}\n\n@media (max-width: @screen-xs-min) {\n    // move prompts above code on small screens\n    div.input {\n        .vbox();\n    }\n}\n\ndiv.prompt_container {\n    display: flex;\n    flex-direction: row;\n    justify-content: space-between;\n    align-items: flex-start;\n    text-align: right;\n}\n\n/* input_area and input_prompt must match in top border and margin for alignment */\ndiv.input_prompt {\n    color: @input_prompt_color;\n    border-top: 1px solid transparent;\n}\n\ndiv.run_this_cell {\n    display: none;\n    cursor: pointer;\n    color: #333;\n    padding-top: 5px;\n    padding-bottom: 5px;\n    padding-left: 2ex;\n    padding-right: 2ex;\n    width: 1ex;\n}\n\n// set the prompt width\ndiv.prompt {\n    min-width: 15ex;\n}\n\n@media (-moz-touch-enabled: 1), (any-pointer: coarse) {\n    div.run_this_cell {\n        display: block;\n    }\n}\n\n// The styles related to div.highlight are for nbconvert HTML output only. This works\n// because the .highlight div isn't present in the live notebook. We could put this into\n// nbconvert, but it easily falls out of sync, can't use our less variables and doesn't\n// help the basic template when paired with our CSS.\n\ndiv.input_area > div.highlight {\n    margin: @code_padding;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n\ndiv.input_area > div.highlight > pre {\n    margin: 0px;\n    border: none;\n    padding: 0px;\n    background-color: transparent;\n}\n","/* The following gets added to the <head> if it is detected that the user has a\n * monospace font with inconsistent normal/bold/italic height.  See\n * notebookmain.js.  Such fonts will have keywords vertically offset with\n * respect to the rest of the text.  The user should select a better font.\n * See: https://github.com/ipython/ipython/issues/1503\n *\n * .CodeMirror span {\n *      vertical-align: bottom;\n * }\n */\n\n.CodeMirror {\n    line-height: @code_line_height;  /* Changed from 1em to our global default */\n    font-size: @notebook_font_size;\n    height: auto;     /* Changed to auto to autogrow */\n    background: none;  /* Changed from white to allow our bg to show through */\n}\n\n.CodeMirror-scroll {\n    /*  The CodeMirror docs are a bit fuzzy on if overflow-y should be hidden or visible.*/\n    /*  We have found that if it is visible, vertical scrollbars appear with font size changes.*/\n    overflow-y: hidden;\n    overflow-x: auto;\n}\n\n.CodeMirror-lines {\n    /* In CM2, this used to be 0.4em, but in CM3 it went to 4px. We need the em value because */\n    /* we have set a different line-height and want this to scale with that. */\n    /* Note that this should set vertical padding only, since CodeMirror assumes\n       that horizontal padding will be set on CodeMirror pre */\n    padding: @code_padding 0;\n}\n\n.CodeMirror-linenumber {\n    // This is needed to fine tune the position of the line numbers because we use the 0.4em in @code_padding\n    // spacing in various places. Fine tuned to look right.\n    padding: 0 8px 0 4px;\n}\n\n.CodeMirror-gutters {\n    // This is needed because our cell has rounded corners, otherwise the gutter area square\n    // corner cuts into the rounded cell border.\n    border-bottom-left-radius: @border-radius-base;\n    border-top-left-radius: @border-radius-base;\n}\n\n.CodeMirror pre {\n    /* In CM3 this went to 4px from 0 in CM2. This sets horizontal padding only,\n    use .CodeMirror-lines for vertical */\n    padding: 0 @code_padding;\n    border: 0;\n    .border-radius(0)\n}\n\n.CodeMirror-cursor {\n    border-left: 1.4px solid black;\n}\n\n// When zoomed out 67% and 33% on a screen of 1440 width x 900 height\n@media screen and (min-width: 2138px) and (max-width: 4319px)  {\n    .CodeMirror-cursor {\n        border-left: 2px solid black;\n    }\n}\n\n// When zoomed out less than 33%\n@media screen and (min-width: 4320px)  {\n    .CodeMirror-cursor {\n        border-left: 4px solid black;\n    }\n}\n","/*\n\nOriginal style from softwaremaniacs.org (c) Ivan Sagalaev <Maniac@SoftwareManiacs.Org>\nAdapted from GitHub theme\n\n*/\n\n@import (reference) \"highlight-refs.less\";\n\n@highlight-base: #000;\n\n.highlight-base{\n  color: @highlight-base;\n}\n\n.highlight-variable{\n  .highlight-base();\n}\n\n.highlight-variable-2{\n  color: lighten(@highlight-base, 10%);\n}\n\n.highlight-variable-3{\n  color: lighten(@highlight-base, 20%);\n}\n\n.highlight-string{\n  color: #BA2121;\n}\n\n//fix issue #4206, overwrite .highlight-comment class, from #408080 to #007979\n.highlight-comment{\n  color: #007979;\n  font-style: italic;\n}\n\n.highlight-number{\n  color: #080;\n}\n\n.highlight-atom{\n  color: #88F;\n}\n\n.highlight-keyword{\n  color: #008000;\n  font-weight: bold;\n}\n\n.highlight-builtin{\n  color: #008000;\n}\n\n.highlight-error{\n  color: #f00;\n}\n\n.highlight-operator{\n  color: #AA22FF;\n  font-weight: bold;\n}\n\n.highlight-meta{\n  color: #AA22FF;\n}\n\n/* previously not defined, copying from default codemirror */\n.highlight-def{ .cm-s-default.cm-def() }\n.highlight-punctuation{ .cm-s-default.cm-punctuation() }\n.highlight-property{ .cm-s-default.cm-property() }\n.highlight-string-2{ .cm-s-default.cm-string-2() }\n.highlight-qualifier{ .cm-s-default.cm-qualifier() }\n.highlight-bracket{ .cm-s-default.cm-bracket() }\n.highlight-tag{ .cm-s-default.cm-tag() }\n.highlight-attribute{ .cm-s-default.cm-attribute() }\n.highlight-header{ .cm-s-default.cm-header() }\n.highlight-quote{ .cm-s-default.cm-quote() }\n.highlight-link{ .cm-s-default.cm-link() }\n\n\n/* apply the same style to codemirror */\n.cm-s-ipython span {\n  &.cm-keyword      { .highlight-keyword() }\n  &.cm-atom         { .highlight-atom() }\n  &.cm-number       { .highlight-number() }\n  &.cm-def          { .highlight-def() }\n  &.cm-variable     { .highlight-variable() }\n  &.cm-punctuation  { .highlight-punctuation() }\n  &.cm-property     { .highlight-property() }\n  &.cm-operator     { .highlight-operator() }\n  &.cm-variable-2   { .highlight-variable-2() }\n  &.cm-variable-3   { .highlight-variable-3() }\n  &.cm-comment      { .highlight-comment() }\n  &.cm-string       { .highlight-string() }\n  &.cm-string-2     { .highlight-string-2() }\n  &.cm-meta         { .highlight-meta() }\n  &.cm-qualifier    { .highlight-qualifier() }\n  &.cm-builtin      { .highlight-builtin() }\n  &.cm-bracket      { .highlight-bracket() }\n  &.cm-tag          { .highlight-tag() }\n  &.cm-attribute    { .highlight-attribute() }\n  &.cm-header       { .highlight-header() }\n  &.cm-quote        { .highlight-quote() }\n  &.cm-link         { .highlight-link() }\n  &.cm-error        { .highlight-error() }\n\n  &.cm-tab {\n    background: url(data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAADAAAAAMCAYAAAAkuj5RAAAAAXNSR0IArs4c6QAAAGFJREFUSMft1LsRQFAQheHPowAKoACx3IgEKtaEHujDjORSgWTH/ZOdnZOcM/sgk/kFFWY0qV8foQwS4MKBCS3qR6ixBJvElOobYAtivseIE120FaowJPN75GMu8j/LfMwNjh4HUpwg4LUAAAAASUVORK5CYII=);\n    background-position: right;\n    background-repeat: no-repeat;\n  }\n}\n","div.output_wrapper {\n    /* this position must be relative to enable descendents to be absolute within it */\n    position: relative;\n    .vbox();\n    // avoid scrolled output overlaying input in some strange circumstances\n    z-index: 1;\n}\n\n/* class for the output area when it should be height-limited */\ndiv.output_scroll {\n    /* ideally, this would be max-height, but FF barfs all over that */\n    height: 24em;\n    /* FF needs this *and the wrapper* to specify full width, or it will shrinkwrap */\n    width: 100%;\n\n    overflow: auto;\n    .corner-all;\n    .box-shadow(inset 0 2px 8px rgba(0, 0, 0, .8));\n    display: block;\n}\n\n/* output div while it is collapsed */\ndiv.output_collapsed {\n    margin: 0px;\n    padding: 0px;\n    .vbox();\n}\n\ndiv.out_prompt_overlay {\n    height: 100%;\n    padding: 0px @code_padding;\n    position: absolute;\n    .corner-all;\n}\n\ndiv.out_prompt_overlay:hover {\n    /* use inner shadow to get border that is computed the same on WebKit/FF */\n    .box-shadow(inset 0 0 1px #000);\n    background: rgba(240, 240, 240, 0.5);\n}\n\ndiv.output_prompt {\n    color: @output_prompt_color;\n}\n\n/* This class is the outer container of all output sections. */\ndiv.output_area {\n    padding: 0px;\n    page-break-inside: avoid;\n    .hbox();\n\n    .MathJax_Display {\n        // Inside a CodeCell, elements are left justified\n        text-align: center !important;\n    }\n\n    .rendered_html {\n        // Inside a CodeCell, elements are left justified\n        table {\n            margin-left: 0;\n            margin-right: 0;\n        }\n    \n        img {\n            margin-left: 0;\n            margin-right: 0;\n        }\n    }\n    \n    img, svg {\n        max-width: 100%;\n        height: auto;\n        &.unconfined {\n            max-width: none;\n        }\n    }\n\n    .mglyph>img {\n        max-width: none;\n    }\n\n    /* Basthon: better rendering for animation controls in FuncAnimation.to_jshtml */\n    .output_subarea {\n        .animation {\n            .anim-controls {\n                .anim-buttons {\n                    button {\n                        padding: 5px 10px;\n                        width: unset;\n                    }\n                }\n            }\n        }\n    }\n}\n\n/* This is needed to protect the pre formating from global settings such\n   as that of bootstrap */\n.output {\n    .vbox();\n}\n\n@media (max-width: @screen-xs-min) {\n    // move prompts above output on small screens\n    div.output_area {\n        .vbox();\n    }\n}\n\ndiv.output_area pre {\n    margin: 0;\n    padding: 1px 0 1px 0;\n    border: 0;\n    vertical-align: baseline;\n    color: @output_pre_color;\n    background-color: transparent;\n    .border-radius(0);\n}\n\n/* This class is for the output subarea inside the output_area and after\n   the prompt div. */\ndiv.output_subarea {\n    // Don't let contents overflow page area.\n    overflow-x: auto;\n    padding: @code_padding;\n    .box-flex1();\n    // appears to be needed for max-width in children to mean anything on Firefox:\n    max-width: calc(~\"100% - 14ex\");\n}\n\ndiv.output_scroll div.output_subarea {\n    // if the output area is scrolling, we don't need scrollbars on the subarea\n    overflow-x: visible;\n}\n\n/* The rest of the output_* classes are for special styling of the different\n   output types */\n\n/* all text output has this class: */\ndiv.output_text {\n    text-align: left;\n    color: @text-color;\n    /* This has to match that of the the CodeMirror class line-height below */  \n    line-height: @code_line_height;\n}\n\n/* stdout/stderr are 'text' as well as 'stream', but execute_result/error are *not* streams */\ndiv.output_stream {\n}\n\ndiv.output_stdout {\n}\n\ndiv.output_stderr {\n    background: var(--output-stderr-bg-color); /* very light red background for stderr */\n}\n\ndiv.output_latex {\n    text-align: left;\n}\n\ndiv.output_html {\n}\n\ndiv.output_png {\n}\n\ndiv.output_jpeg {\n}\n\n/* Empty output_javascript divs should have no height */\ndiv.output_javascript:empty {\n    padding: 0;\n}\n\n.js-error {\n    color: darkred;\n}\n\n/* raw_input styles */\n\ndiv.raw_input_container {\n    line-height: @code_line_height;\n    // for some reason, em padding doesn't compute the same for raw_input\n    // that is not the first input, but px does\n    padding-top: 5px;\n}\n\npre.raw_input_prompt {\n    /* nothing needed here. */\n}\n\ninput.raw_input {\n    font-family: @font-family-monospace;\n    font-size: inherit;\n    color: inherit;\n    width: auto;\n    /* make sure input baseline aligns with prompt */\n    vertical-align: baseline;\n    /* padding + margin = 0.5em between prompt and cursor */\n    padding: 0em 0.25em;\n    margin: 0em 0.25em;\n}\n\ninput.raw_input:focus {\n    box-shadow: none;\n}\n\np.p-space {\n    margin-bottom: 10px;\n}\n\ndiv.output_unrecognized {\n  padding: 5px;\n  font-weight: bold;\n  color: red;\n  // remove decoration from link\n  a {\n      color: inherit;\n      text-decoration: none;\n\n      &:hover {\n          color: inherit;\n          text-decoration: none;\n      }\n  }\n}\n",".rendered_html {\n\n    color: @text-color;\n    em {font-style: italic;}\n    strong {font-weight: bold;}\n    u {text-decoration: underline;}\n    :link {text-decoration: underline;}\n    :visited {text-decoration: underline;}\n\n    // For a 14px base font size this goes as:\n    // font-size = 26, 22, 18, 14, 12, 12\n    // margin-top = 14, 14, 14, 14, 8, 8\n    h1 {font-size: 185.7%; margin: 1.08em 0 0 0; font-weight: bold; line-height: 1.0;}\n    h2 {font-size: 157.1%; margin: 1.27em 0 0 0; font-weight: bold; line-height: 1.0;}\n    h3 {font-size: 128.6%; margin: 1.55em 0 0 0; font-weight: bold; line-height: 1.0;}\n    h4 {font-size: 100%; margin: 2em 0 0 0; font-weight: bold; line-height: 1.0;}\n    h5 {font-size: 100%; margin: 2em 0 0 0; font-weight: bold; line-height: 1.0; font-style: italic;}\n    h6 {font-size: 100%; margin: 2em 0 0 0; font-weight: bold; line-height: 1.0; font-style: italic;}\n\n    // Reduce the top margins by 14px compared to above\n    h1:first-child {margin-top: 0.538em;}\n    h2:first-child {margin-top: 0.636em;}\n    h3:first-child {margin-top: 0.777em;}\n    h4:first-child {margin-top: 1em;}\n    h5:first-child {margin-top: 1em;}\n    h6:first-child {margin-top: 1em;}\n\n    ul:not(.list-inline),\n    ol:not(.list-inline) {padding-left: 2em;}\n    ul {list-style:disc;}\n    ul ul {\n      list-style:square;\n      margin-top: 0;\n    }\n    ul ul ul {list-style:circle;}\n    ol {list-style:decimal;}\n    ol ol {\n      list-style:upper-alpha; \n      margin-top: 0;\n    }\n    ol ol ol {list-style:lower-alpha; }\n    ol ol ol ol {list-style:lower-roman; }\n    /* any extras will just be numbers: */\n    ol ol ol ol ol {list-style:decimal;}\n    * + ul {margin-top: 1em;}\n    * + ol {margin-top: 1em;}\n\n    hr {\n        color: @rendered_html_border_color;\n        background-color: @rendered_html_border_color;\n    }\n\n    pre {\n        margin: 1em 2em;\n        padding: 0px;\n        background-color: @body-bg;\n    }\n\n    code {\n        background-color: #eff0f1;\n    }\n\n    p code {\n        padding: 1px 5px;\n    }\n\n    pre code {background-color: @body-bg;}\n\n    pre, code {\n        border: 0;\n        color: @text-color;\n        font-size: 100%;\n    }\n\n    blockquote {margin: 1em 2em;}\n\n    table {\n        margin-left: auto;\n        margin-right: auto;\n        border: none;\n        border-collapse: collapse;\n        border-spacing: 0;\n        color: @rendered_html_border_color;\n        font-size: 12px;\n        table-layout: fixed;\n    }\n    thead {\n        border-bottom: 1px solid @rendered_html_border_color;\n        vertical-align: bottom;\n    }\n    tr, th, td {\n        text-align: right;\n        vertical-align: middle;\n        padding: 0.5em 0.5em;\n        line-height: normal;\n        white-space: normal;\n        max-width: none;\n        border: none;\n    }\n    th {\n        font-weight: bold;\n    }\n    tbody tr:nth-child(odd) {\n        background: #f5f5f5;\n    }\n    tbody tr:hover {\n        background: rgba(66, 165, 245, 0.2);\n    }\n    * + table {margin-top: 1em;}\n\n    p {text-align: left;}\n    * + p {margin-top: 1em;}\n\n    img {\n        display: block;\n        margin-left: auto;\n        margin-right: auto;\n    }\n    * + img {margin-top: 1em;}\n    \n    img, svg {\n        max-width: 100%;\n        height: auto;\n        &.unconfined {\n            max-width: none;\n        }\n    }\n\n    // Override bootstrap settings, see #1390\n    .alert {margin-bottom: initial;}\n    * + .alert {margin-top: 1em;}\n}\n\n[dir=\"rtl\"] .rendered_html {\n    p {\n        text-align : right;\n    }\n}\n\n","div.text_cell {\n    .hbox();\n}\n@media (max-width: @screen-xs-min) {\n    // remove prompt indentation on small screens\n    div.text_cell > div.prompt {\n        display: none;\n    }\n}\n\ndiv.text_cell_render {\n    /*font-family: \"Helvetica Neue\", Arial, Helvetica, Geneva, sans-serif;*/\n    outline: none;\n    resize: none;\n    width:  inherit;\n    border-style: none;\n    padding: 0.5em 0.5em 0.5em @code_padding;\n    color: @text-color;\n    .border-box-sizing();\n}\n\na.anchor-link:link {\n  text-decoration: none;\n  padding: 0px 20px;\n  visibility: hidden;\n}\n\nh1,h2,h3,h4,h5,h6 {\n    &:hover .anchor-link {\n        visibility: visible;\n    }\n}\n\n.text_cell.rendered .input_area {\n    display: none;\n}\n\n.text_cell.rendered .rendered_html {\n    overflow-x: auto;\n    \n    // Content in the y direction should cause the rendered content to grow,\n    // the overflow-x: auto causes chrome to assume the same of y, so we need\n    // to tell it explicitly otherwise.\n    overflow-y: hidden;\n}\n\n.text_cell.rendered .rendered_html {\n\ttr, th, td {\n\t\tmax-width: none;\n\t}\n}\n\n.text_cell.unrendered .text_cell_render {\n    display:none;\n}\n\n.text_cell .dropzone .input_area {\n  border: 2px dashed #bababa;\n  margin: -1px;\n}\n\n.cm-header-1,\n.cm-header-2,\n.cm-header-3,\n.cm-header-4,\n.cm-header-5,\n.cm-header-6 {\n    font-weight: bold;\n    font-family: @font-family-sans-serif;\n}\n\n.cm-header-1 { font-size: 185.7%; }\n.cm-header-2 { font-size: 157.1%; }\n.cm-header-3 { font-size: 128.6%; }\n.cm-header-4 { font-size: 110%; }\n.cm-header-5 {\n    font-size: 100%;\n    font-style: italic;\n}\n.cm-header-6 {\n    font-size: 100%;\n    font-style: italic;\n}\n","@media (max-width: @screen-xs-max) {\n    // remove bootstrap-responsive's body padding on small screens\n    .notebook_app {\n        padding-left: 0px;\n        padding-right: 0px;\n    }\n}\n\n#ipython-main-app {\n    .border-box-sizing();\n    height: 100%;\n}\n\ndiv#notebook_panel {\n    margin: 0px;\n    padding: 0px;\n    .border-box-sizing();\n    height: 100%;\n}\n\ndiv#notebook {\n    font-size: @notebook_font_size;\n    line-height: @notebook_line_height;\n    overflow-y: hidden;\n    overflow-x: auto;\n    width: 100%;\n    /* This spaces the page away from the edge of the notebook area */\n    padding-top: @page-header-padding;\n    padding-bottom: @page-header-padding;\n    margin: 0px;\n    outline: none;\n    .border-box-sizing();\n    min-height: 100%;\n}\n\n#notebook-container{\n    @media not print{\n        padding: @page-padding;\n        background-color :  @page-color;\n        min-height: @page-min-height;\n        .box-shadow(@global-shadow);\n    }\n    @media print {\n        width: 100%;\n    }\n}\n\ndiv.ui-widget-content {\n    border: 1px solid @border_color;\n    outline: none;\n}\n\npre.dialog {\n    background-color: @cell_background;\n    border: 1px solid #ddd;\n    .corner-all;\n    padding: 0.4em;\n    padding-left: 2em;\n}\n\np.dialog {\n    padding : 0.2em;\n}\n\n/* Word-wrap output correctly.  This is the CSS3 spelling, though Firefox seems\n   to not honor it correctly.  Webkit browsers (Chrome, rekonq, Safari) do.\n */\npre, code, kbd, samp { white-space: pre-wrap; }\n\n#fonttest {\n    font-family: @font-family-monospace;\n}\n\np {\n    margin-bottom:0;\n}\n\n.end_space {\n    min-height: 100px;\n    transition: height .2s ease;\n}\n\n.notebook_app > #header {\n    .box-shadow(@global-shadow);\n}\n\n.notebook_app{\n    @media not print {\n        background-color: @page