er to rendered markdown cells\n    border: @border_width solid @light_border_color;\n}\n\n.celltoolbar {\n    font-size: 87%;\n    padding-top: 3px;\n}\n\n.celltoolbar select {\n    .form-control();\n    .input-sm();\n    // undo some of the sizing caused by the above mixins\n    width: inherit;\n    font-size: inherit;\n    height: 22px;\n    padding: 0px;\n    \n    display: inline-block;\n\n}\n\n.celltoolbar label {\n    margin-left: 5px;\n    margin-right: 5px;\n}\n","\n\n.tags_button_container {\n    width: 100%;\n    display: flex;\n}\n\n.tag-container {\n    display: flex;\n    flex-direction: row;\n    flex-grow: 1;\n    overflow: hidden;\n    position: relative;\n}\n\n.tag-container > * {\n    margin: 0 4px;\n}\n\n.remove-tag-btn {\n    margin-left: 4px;\n}\n\n.tags-input {\n    display: flex;\n}\n\n.cell-tag:last-child:after {\n    content: \"\";\n    position: absolute;\n    right: 0;\n    width: 40px;\n    height: 100%;\n    /* Fade to background color of cell toolbar */\n    background: linear-gradient(to right, rgba(0,0,0,0), #EEE);\n}\n\n.tags-input > * {\n    margin-left: 4px;\n}\n\n.cell-tag,\n.tags-input input,\n.tags-input button {\n    .form-control();\n    .input-sm();\n    // undo some of the sizing caused by the above mixins\n    box-shadow: none;\n    width: inherit;\n    font-size: inherit;\n    height: 22px;\n    line-height: 22px;\n    padding: 0px 4px;\n\n    display: inline-block;\n}\n\n.cell-tag,\n.tags-input button {\n    padding: 0px 4px;\n}\n\n.cell-tag {\n    background-color: #fff;\n    white-space: nowrap;\n}\n\n.tags-input input[type=text]:focus {\n    outline: none;\n    box-shadow: none;\n    border-color: #ccc;\n}\n",".completions {\n    position: absolute;\n    z-index: 110;\n    overflow: hidden;\n    border: 1px solid @border_color;\n    .corner-all;\n    .box-shadow(0px 6px 10px -1px #adadad);\n    line-height: 1;\n}\n\n.completions select {\n    background: white;\n    outline: none;\n    border: none;\n    padding: 0px;\